import * as React from "react"
import { Link } from "gatsby"

// styles
const pageStyles = {
  color: "#232129",
  padding: "96px",
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}
const headingStyles = {
  marginTop: 0,
  marginBottom: 64,
  maxWidth: 320,
}

const paragraphStyles = {
  marginBottom: 48,
  fontSize: 18,
}
const codeStyles = {
  color: "#8A6534",
  padding: 4,
  backgroundColor: "#FFF4DB",
  fontSize: "1.25rem",
  borderRadius: 4,
}

// markup
const NotFoundPage = () => {
  return (
    <main style={pageStyles}>
      <title>Not found</title>
      <h1 style={headingStyles}>404 - <i>Page not found</i></h1>
      <p style={paragraphStyles}>
        <i>Sorry love its not here {"<3"}
        </i><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
        {process.env.NODE_ENV === "development" ? (
          <>
          </>
        ) : null}
        <br />
      </p>
      <p style={codeStyles}>
      <Link to="/"> click here to go home</Link>
      </p>
    </main>
  )
}

export default NotFoundPage
